import Layout from '../components/MyLayout';
import Link from 'next/link';

const PostLink = props => (
  <li>
    <Link href={`/post?title=${props.title}`}>
      <a>{props.title}</a>
    </Link>
  </li>
);

const index = () => (
  <Layout>
    <h1>My Blog</h1>
    <ul>
      {['Hello Next.js', 'Learn Next.js is awesome', 'Deploy apps with Zeit'].map(title => {
        return (
          <PostLink
            key={title}
            title={title}
          />
        );
      })}
    </ul>
  </Layout>
);


export default index;
